import React, { Component } from 'react'
import {inject , observer} from 'mobx-react'
import './index.scss'
@inject('goods')
@observer
class CartImg extends Component {
    checkout() {
        let productIds = this.props.item.product_id
        this.props.goods.getCartChecked({isChecked:this.props.item.checked === 1 ? 0 : 1,productIds})
    }
    render() {
        return (
            <div className='main_checkout' onClick={() => {this.checkout()}}>
                {
                    this.props.item.checked === 1 ? 
                    <img src='data:image/png;base64,' alt='' /> :
                    <img src="data:image/png;base64," alt=""/> 
                }
            </div>
        )
    }
}
export default CartImg